.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

/*
|--------------------------------------------------------------------------
| Image Filter
|--------------------------------------------------------------------------
*/
/* --- 1. Filter Grayscale --- */
/* --- 1.1 Variables - grayscale image filter --- */
/* --- 1.1.1 Variable Default --- */
.ilist, .bg-glitchy {
  filter: grayscale(1);
}

.ilist:hover, .bg-glitchy:hover {
  filter: none;
}

/* --- 1.1.2 Variable Invert --- */
.ilist-inv {
  filter: none;
}

.ilist-inv:hover {
  filter: grayscale(1);
  transition: 0.40s both;
}

/* --- 1.2 Classes - grayscale image filter --- */
/* --- 1.2.1 Class Default --- */
/* --- 1.2.2 Class Invert --- */
/* --- 2. Filter Invisible --- */
/* --- 2.1 Variables - invisible image filter --- */
/* --- 2.1.1 Variable Default --- */
.ilist-i {
  filter: none;
}

.ilist-i:hover {
  filter: opacity(0.5);
}

/* --- 2.1.2 Variable Invert --- */
.ilist-i-inv {
  filter: opacity(0.5);
}

.ilist-i-inv:hover {
  filter: none;
}

/* --- 2.2.1 Class Default --- */
/* --- 2.2.2 Class Invert --- */
/* --- 3. Filter Blur --- */
/* --- 3.1 Variables - blur image filter --- */
/* --- 3.1.1 Variable Default --- */
.ilist-b {
  filter: none;
}

.ilist-b:hover {
  filter: blur(2px);
}

/* --- 3.1.2 Variable Invert --- */
.ilist-b-inv {
  filter: blur(2px);
}

.ilist-b-inv:hover {
  filter: none;
}

/* --- 3.2.1 Class Default --- */
/* --- 3.2.2 Class Invert --- */
/* --- 4. Filter Contrast --- */
/* --- 4.1 Variables - contrast image filter --- */
/* --- 4.1.1 Variable Default --- */
.ilist-c {
  filter: none;
}

.ilist-c:hover {
  filter: contrast(1.1);
}

/* --- 4.1.2 Variable Invert --- */
.ilist-c-inv {
  filter: contrast(1.1);
}

.ilist-c-inv:hover {
  filter: none;
}

/* --- 4.2.1 Class Default - contrast image filter --- */
